// app/home/page.tsx
"use client";

import dynamic from "next/dynamic";
import { Canvas } from "reaflow";

import styles from "./editorStyle.module.css";
import mockJson from "@/assets/mock/jsonMock.json";
import { useJsonGraph } from "@/hooks/useJsonGraph";
import { useEffect } from "react";

const MonacoEditor = dynamic(() => import("@monaco-editor/react"), {
  ssr: false,
});

export default function HomePage() {
  const { input, nodes, edges, error, handleChange } = useJsonGraph();

  useEffect(() => {
    console.log("nodes: ", nodes);
  }, [nodes]);

  return (
    <div className={styles.editor}>
      <MonacoEditor
        height="100%"
        language="json"
        defaultLanguage="json"
        defaultValue={JSON.stringify(mockJson, null, 2)}
        options={{
          minimap: { enabled: false },
          readOnly: false,
          wordWrap: "on",
          formatOnPaste: true,
          formatOnType: true,
          tabSize: 2,
          scrollBeyondLastLine: false,
        }}
        onChange={(val) => {
          console.log("val: ", val);
          handleChange(val ?? "");
        }}
      />
      <Canvas></Canvas>
    </div>
  );
}
